import { Link } from 'gatsby';
import React, { FC, PropsWithChildren } from 'react';
import { colors } from '../../../utils/colors';
import RightMiddleArrow from '../Icons/Arrows/RightMiddleArrow';
import {
  ArticleCardBody,
  ArticleCardFooter,
  ArticleCardImage,
  ArticleCardTitle,
  ArticleCardType,
  StyledArticleCardItemLarge,
  StyledArticleCardItemMedium,
  StyledArticleCardItemSmall,
} from './styled';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import { ArticlePreviewInfo, StaticCardPreviewInfo } from '../../../types';

export enum ArticleType {
  PressRelease = 'Press release',
  Update = 'Update',
  Featured = 'Featured',
}

export enum CardSize {
  S = 'S',
  M = 'M',
  L = 'L',
}

type ArticleCardProps = {
  article: ArticlePreviewInfo | StaticCardPreviewInfo;
  cardSize?: CardSize;
};

const ArticleCardItemRenderByCardSize = ({
  cardSize,
  children,
}: PropsWithChildren<{ cardSize?: CardSize }>) => {
  switch (cardSize) {
    case CardSize.L:
      return <StyledArticleCardItemLarge>{children}</StyledArticleCardItemLarge>;
    case CardSize.M:
      return <StyledArticleCardItemMedium>{children}</StyledArticleCardItemMedium>;
    case CardSize.L:
      return <StyledArticleCardItemSmall>{children}</StyledArticleCardItemSmall>;
    default:
      return <StyledArticleCardItemSmall>{children}</StyledArticleCardItemSmall>;
  }
};

const ArticleCardItem: FC<ArticleCardProps> = ({ article, cardSize }) => {
  const { date, title, url } = article;
  const articlePreviewInfo = article as ArticlePreviewInfo;
  const staticArticlePreviewInfo = article as StaticCardPreviewInfo;
  return (
    <ArticleCardItemRenderByCardSize cardSize={cardSize}>
      {articlePreviewInfo.previewImage ? (<ArticleCardImage>
        <Link to={url}>
          <GatsbyImage
            image={
              articlePreviewInfo.previewImage.childrenImageSharp?.[0]
                ?.gatsbyImageData as IGatsbyImageData
            }
            alt={articlePreviewInfo.title}
          />
        </Link>
      </ArticleCardImage>) :
        staticArticlePreviewInfo.previewStaticImage
      }
      <ArticleCardBody to={url}>
        <div>
          <ArticleCardType>{articlePreviewInfo.type}</ArticleCardType>
          <ArticleCardTitle>{title}</ArticleCardTitle>
        </div>
        <ArticleCardFooter>
          <div>{date}</div>
          <div>
            <RightMiddleArrow color={colors.primary.blue} />
          </div>
        </ArticleCardFooter>
      </ArticleCardBody>
    </ArticleCardItemRenderByCardSize>
  );
};

export default ArticleCardItem;
