import { Link } from "gatsby";
import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";

export const ArticleCardTitle = styled.div`
  font-weight: 600;
  font-size: 0.9rem;
  line-height: 1.1rem;
  transition: 0.3s;
  @media ${device.laptop} {
    font-size: 1.6rem;
    line-height: 1.9rem;
  }
`;

export const StyledArticleCardItem = styled.div`
  background-color: ${colors.secondary.gray100};
  border-radius: 14px;
  font-family: "Rubik";
  display: flex;
  width: 100%;
  height: auto;

  &:hover {
    ${ArticleCardTitle} {
      color: ${colors.secondary.orange};
    }
  }
`;


export const ArticleCardImage = styled.div`
  img {
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
    border-radius: 14px 14px 0 0;
  }
`;

export const ArticleCardType = styled.div`
  font-weight: 600;
  font-size: 0.6rem;
  text-transform: uppercase;
  color: ${colors.primary.gray500};
  @media ${device.laptop} {
    font-size: 0.7rem;
  }
`;

export const ArticleCardBody = styled(Link)`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  height: 100%;
  box-sizing: border-box;

  & > div:first-child {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  @media ${device.laptop} {
    padding: 40px;
    width: 50%;
  }
`;

export const ArticleCardFooter = styled.div`
  font-size: 0.7rem;
  color: ${colors.primary.gray500};
  font-weight: 400;
  line-height: 0.9rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media ${device.laptop} {
    font-size: 0.8rem;
    line-height: 1rem;
  }
`;

export const StyledArticleCardItemLarge = styled(StyledArticleCardItem)`
  flex-direction: row;
  justify-content: flex-start;
  gap: 0;

  ${ArticleCardBody} {
    gap: 80px;
  }

  ${ArticleCardImage} {
    width: 50%;
    height: 100%;
    object-fit: cover;
    div {
      width: 100% !important;
      height: 100% !important;
    }
    img {
      border-radius: 14px 0 0 14px;
    }
  }
`;

export const StyledArticleCardItemMedium = styled(StyledArticleCardItem)`
  flex-direction: column;
  justify-content: flex-start;
  gap: 0;

  ${ArticleCardImage} {
    width: 100%;
    object-fit: cover;
    min-height: 220px;
    max-height: 220px;
    div {
      width: 100% !important;
      height: 100% !important;
    }

    img {
      border-radius: 14px 14px 0 0;
    }
  }

  ${ArticleCardBody} {
    width: 100%;
    gap: 25px;
    box-sizing: border-box;
  }

  @media ${device.tablet} {
    ${ArticleCardImage} {
      min-height: 350px;
      max-height: 350px;
      img {
        border-radius: 14px 14px 0 0;
      }
    }
  }

  @media ${device.laptop} {
    ${ArticleCardImage} {
      min-height: 250px;
      max-height: 250px;
      img {
        border-radius: 14px 14px 0 0;
      }
    }
  }

  ${ArticleCardTitle} {
    @media ${device.laptop} {
      font-size: 1.1rem;
      line-height: 1.4rem;
    }
  }
`;

export const StyledArticleCardItemSmall = styled(StyledArticleCardItem)`
  flex-direction: row;
  justify-content: flex-start;
  gap: 0;

  ${ArticleCardBody} {
    gap: 25px;
  }

  ${ArticleCardImage} {
    width: 50%;
    object-fit: cover;
    div {
      width: 100% !important;
      height: 100% !important;
    }

    img {
      border-radius: 14px 0 0 14px;
    }
  }

  ${ArticleCardBody} {
    width: 70%;
  }

  ${ArticleCardTitle} {
    @media ${device.laptop} {
      font-size: 1.1rem;
      line-height: 1.4rem;
    }
  }
`;

export const ArticleCardImageContainer = styled.div`
  box-sizing: border-box;
  padding: 15px;
`;
