import React, { FC } from "react";
import { Text } from "../common";
import SearchIcon from "../common/Icons/SearchIcon/SearchIcon";
import { SearchInput, StyledNewsPageHeader } from "./styled";

interface Props {
  searchString: string;
  setSearchString: (value: string) => void;
}

export const NewsPageHeader: FC<Props> = ({
  searchString,
  setSearchString,
}) => {
  return (
    <StyledNewsPageHeader>
      <div>
        <Text.Title.Bold.lg>Latest A2P Messaging News</Text.Title.Bold.lg>
      </div>
      <SearchInput>
        <SearchIcon />
        <input
          type="text"
          placeholder="Search articles"
          value={searchString}
          onChange={(e) => setSearchString(e.currentTarget.value)}
        ></input>
      </SearchInput>
    </StyledNewsPageHeader>
  );
};
